.container a:link, .container a:visited, .container a:hover, .container a:active {
	text-emphasis: none;
    text-decoration: none;
}

.container div:global(.wrapper) > * {
	margin: 20px 0 10px 0;
}

.container .description {
	color: rgba(20, 20, 20. 0.8);
	display: block;
	font-size: 18px;
	line-height: 1.5em;
}

.subtitle {
	display: flex;
	width: 100%;
	justify-content: center;
	box-sizing: border-box;
	padding-top: 20px;
	font-weight: 300;
	font-style: italic;
}

/* Sections */

:global(#better) {
	background-color: #ffebef;
}
:global(#contact) {
	background-color: #fccbd3;
}

.container li {
	position: relative;
	padding: 5px 20px;
}
.liPoint {
	content: ' ';
	position: absolute;
	height: 1.5em;
	width: 1.5em;
	top: -1px;
	left: calc(-20px - 15px);
	z-index: -1;
	border-radius: 10px;
	opacity: 1;
	transition: opacity 0.33s ease;
}
.container li:hover .liPoint {
	opacity: 0.5;
}

.imageContainer {
	position: relative;
	display: grid;
}
.screenshot {
	max-width: 300px;
	position: relative;
	grid-column: 1;
	grid-row: 1;
	box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.2);
}

/* Form */

.formContainer {
	display: flex;
	justify-content: center;
	width: 100%;
}
.form {

	display: grid;
	grid-auto-flow: row;
	text-align: left;
	width: 100%;
	max-width: 500px;
	row-gap: 10px;
}

.form input, .form textarea {
	outline: none;
	border: none;
	border-radius: 5px;
	min-height: 2em;
	padding: 10px;
	box-sizing: border-box;
}

.form textarea {
	min-height: 10em;
}

.form input[type='submit'] {
	background-color: white;
	font-family: 'Montserrat',"Source Sans Pro","Open Sans","San Francisco",Helvetica,Arial,sans-serif;
	font-size: small;
}